import { h, Component } from 'preact';
import { observer } from 'mobx-preact';
import util from 'preact-util';

import LineGraph from '../../components/lineGraph';
import wutil from '../../lib/wifet-util';

const forcastData = wutil.makeRandomData({ cnt: 500, min: 5, max: 15, onlyUp: false, inputData: [], diff: 1 });
const aiData = wutil.makeRandomData({ cnt: 500, min: 0, max: 30, onlyUp: false, inputData: forcastData, diff: 1, maxSkew: 10 });
const windData = wutil.makeRandomData({ cnt: 500, min: 0, max: 30, onlyUp: false, inputData: aiData, diff: 1, maxSkew: 10 });
const priceData = wutil.makeRandomData({ cnt: 500, min: 0, max: 200, onlyUp: false, inputData: [], diff: 5 });
const demandData = wutil.makeRandomData({ cnt: 500, min: 0, max: 4000, onlyUp: false, inputData: [], diff: 100 });

const productionData = wutil.calcData(windData, wutil.calcProductionData);
const productionBoostData = wutil.addPercent(productionData, 10, 100);
const productionLongLifeData = wutil.addPercent(productionData, -5, 100);


const initialState = {
	forcastData,
	aiData,
	windData,
	productionData,
	productionBoostData,
	productionLongLifeData,
	priceData,
	demandData,
};

@observer
class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadAll();	
    }

	async loadAll() {
		const { appState, userStore } = this.props.stores;
        appState.getFingerprint();
        await userStore.getInfo();
    }

	render() {
		const { windData, productionData, productionBoostData, productionLongLifeData, priceData, demandData } = this.state;
		// const { userStore } = this.props.stores;
		// const { user } = userStore;
		return (
			<div class='container-fluid mt-5 mb-5 pt-2'>
				<div class='row'>
					<div class='col-12'>
						<h1>Home</h1>

						<div class='d-flex'>
							<div class='p-2 flex-grow-1 bg-light mr-2'>
								<i class='fas fa-chart-bar' /> Production / <i class='fas fa-thermometer' /> Wear
								<LineGraph
									stores={this.props.stores}
									data={productionData}
									dataB={productionBoostData}
									dataC={productionLongLifeData}
									legend='Normal production'
									legendB='Boost 10%'
									legendC='Long life -10%'
									width={1200}
									height={200}
									yMax={500}
									yMin={0}
									showYTicks={1}
									yTicks='["500Mw", "400Mw", "300Mw", "200Mw", "100Mw", "0Mw"]'
									unit='Mw'
									showXTicks={1}
									marker={400}
								/>
							</div>
							{/* <div class='p-2 bg-light'>
								<i class='fas fa-battery-three-quarters' /> Wear status
							</div> */}
						</div>

						<div class='d-flex mt-2'>
							<div class='p-2 flex-fill bg-light mr-2'>
								<i class='fas fa-cloud-sun-rain' /> Weather
								<LineGraph
									stores={this.props.stores}
									data={windData}
									dataB={forcastData}
									dataC={aiData}
									legend='Wind'
									legendB='Wind forcast'
									legendC='Wind AI prediction'
									width={1200}
									height={200}
									yMax={30}
									yMin={0}
									showYTicks={1}
									yTicks='["30m/s", "25m/s", "20m/s", "15m/s", "10m/s", "5m/s", "0m/s"]'
									unit='m/s'
									showXTicks={1}
									marker={400}
								/>
							</div>
						</div>
						<div class='d-flex mt-2'>
							<div class='p-2 flex-fill bg-light mr-2'>
								<i class='fas fa-chart-line' /> Electricity prices
								<LineGraph
									stores={this.props.stores}
									data={priceData}
									legend='Price'
									width={1200}
									height={200}
									yMax={200}
									yMin={0}
									showYTicks={1}
									yTicks='["2NOK", "1.5NOK", "1NOK", "0.5NOK", "0NOK"]'
									unit='Ã¸re'
									showXTicks={1}
									marker={400}
								/>
							</div>
						</div>
						<div class='d-flex mt-2'>
							<div class='p-2 flex-fill bg-light'>
								<i class='fas fa-hand-holding-usd' /> Demand
								<LineGraph
									stores={this.props.stores}
									data={demandData}
									legend='Demand'
									width={1200}
									height={200}
									yMax={4000}
									yMin={0}
									showYTicks={1}
									yTicks='["4TW", "3TW", "2TW", "1TW", "0TW"]'
									unit='Mw'
									showXTicks={1}
									marker={400}
								/>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Home;
